
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain) */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

@font-face {
  font-family: 'os-sb';
  src: url("fonts/os-sb/os-sb.eot");
  src: url("fonts/os-sb/os-sb.eot?#iefix") format("embedded-opentype"), url("fonts/os-sb/os-sb.woff") format("woff"), url("fonts/os-sb/os-sb.ttf") format("truetype");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'os';
  src: url("fonts/os/os.eot");
  src: url("fonts/os/os.eot?#iefix") format("embedded-opentype"), url("fonts/os/os.woff") format("woff"), url("fonts/os/os.ttf") format("truetype");
  font-weight: normal;
  font-style: normal; }

* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-transition: all 0.3s ease-in;
  -o-transition: all 0.3s ease-in;
  transition: all 0.3s ease-in; }

input, button {
  outline: none;
  border: 0;
  margin: 0;
  padding: 0;
  font-family: 'os';
  color: #1f2639; }

.link {
  color: inherit;
  text-decoration: none; }
  .link:hover {
    color: #b4bdd6; }

.ftitle {
  font-family: 'os-sb';
  margin-bottom: 25px; }

.fitem {
  line-height: 1.846; }

.wrapper {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  background: #192431; }

.footer {
  width: 70%;
  max-width: 1230px;
  padding: 60px 0;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between; }
  @media screen and (max-width: 960px) {
    .footer {
      width: 80%;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -webkit-flex-direction: column;
      -ms-flex-direction: column;
      flex-direction: column; } }
  .footer__left, .footer__right {
    -webkit-box-flex: 1;
    -webkit-flex: 1 1 50%;
    -ms-flex: 1 1 50%;
    flex: 1 1 50%;
    max-width: 50%; }
    @media screen and (max-width: 960px) {
      .footer__left, .footer__right {
        -webkit-box-flex: 0;
        -webkit-flex: 0 0 auto;
        -ms-flex: 0 0 auto;
        flex: 0 0 auto;
        max-width: 100%; } }
  .footer__desc, .footer__menu, .footer__subscr {
    color: #f6f7fa;
    font-family: 'os';
    font-size: 13px; }

.fmenu {
  width: 85%;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between; }
  @media screen and (max-width: 960px) {
    .fmenu {
      width: 100%;
      -webkit-box-pack: start;
      -webkit-justify-content: flex-start;
      -ms-flex-pack: start;
      justify-content: flex-start; } }
  @media screen and (max-width: 480px) {
    .fmenu {
      -webkit-flex-wrap: wrap;
      -ms-flex-wrap: wrap;
      flex-wrap: wrap; } }
  .fmenu__column {
    padding-right: 10px;
    margin-top: 70px; }
    @media screen and (max-width: 960px) {
      .fmenu__column {
        padding-right: 10vw;
        margin-top: 45px; } }
    @media screen and (max-width: 480px) {
      .fmenu__column {
        min-width: 45%;
        -webkit-box-flex: 1;
        -webkit-flex: 1 1 auto;
        -ms-flex: 1 1 auto;
        flex: 1 1 auto; } }

.fsubscr {
  margin-left: 15%; }
  @media screen and (max-width: 960px) {
    .fsubscr {
      margin-left: 0;
      margin-top: 45px; } }
  .fsubscr__form {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 15px; }
    @media screen and (max-width: 960px) {
      .fsubscr__form {
        width: 50%; } }
    @media screen and (max-width: 480px) {
      .fsubscr__form {
        width: 100%; } }
  .fsubscr__input {
    width: 80%;
    height: 45px;
    -webkit-box-flex: 0;
    -webkit-flex: 0 0 auto;
    -ms-flex: 0 0 auto;
    flex: 0 0 auto;
    float: left;
    padding: 10px 15px 10px 10px;
    background-color: #f6f7fa;
    border-top-left-radius: 2px;
    border-bottom-left-radius: 2px; }
  .fsubscr__submit {
    min-width: 55px;
    min-height: 45px;
    -webkit-box-flex: 0;
    -webkit-flex: 0 0 auto;
    -ms-flex: 0 0 auto;
    flex: 0 0 auto;
    background-color: #82ca9c;
    border-top-right-radius: 2px;
    border-bottom-right-radius: 2px;
    cursor: pointer;
    position: relative; }
    .fsubscr__submit::before {
      position: absolute;
      content: '';
      background: url("../img/ok.png") no-repeat;
      background-size: contain;
      width: 30px;
      height: 30px;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
      z-index: 3; }
  .fsubscr__desc {
    width: 70%;
    font-size: 12px;
    line-height: 1.4;
    opacity: 0.3; }
    @media screen and (max-width: 960px) {
      .fsubscr__desc {
        width: 50%; } }
    @media screen and (max-width: 480px) {
      .fsubscr__desc {
        width: 100%; } }


